<template>
  <div class="Japanese">
    <h3 class="nav_btn" v-on:click="changeShowType()"></h3>
    <div class="nav_bg">
      <td class="nav" v-for="(item,key) in nav_title" v-bind:key="item.id">
        <h2 :class="{'nav_item': true,
        'nav_active': key === nav_chose, 'left_radius': key===0, 'right_radius': key === nav_title.length-1}"
            v-on:click = "onNavSelected(key)" >{{item.name}}</h2>
      </td>
    </div>

    <table class="container">
      <tr v-for="v in current" class="row">
        <td v-for="item in v" :class="{'normal_row': nav_chose!==2,'less_row':nav_chose===2}" v-on:click = "onItemSelected(item, $event)">
          <div >
            <h4 :class="{'letter': true, 'active': item.name === selected }"> {{showType === 0? item.left:item.right}}</h4>
            <h4 id="name" > {{item.name}}</h4>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'Japanese',
    computed: mapGetters({
      language: 'getCurLan'
    }),
    created(){
      this.gamemuiscs1 = new Audio()
      this.current = this.V_less
      switch (this.language){
        case 'Chinese':
          this.nav_title = [{name:''}, {name:''},{name:''}];
          break
        case 'English':
          this.nav_title = [{name:'V_less'}, {name:'Voiced'},{name:'Bend'}];
          break
      }
    },

    methods: {
      onItemSelected (item, event) {
        this.selected = item.name
//        event.stopPropagation();
        if(item.raw !== ''){
          this.gamemuiscs1 = new Audio("./static/rawes/"+item.raw);
          this.gamemuiscs1.play();
        }
      },
      onNavSelected (key) {
        this.nav_chose = key
        switch (key){
          case 0:
            this.current = this.V_less
            break
          case 1:
            this.current = this.Voiced
            break
          case 2:
            this.current = this.Bend
            break
        }
      },
      changeShowType(){
        this.showType ^= 1;
      }
    },

    data () {
      return {
        nav_chose: 0,
        selected:'',
        showType: 0,  //0 1 
        nav_title: [{name:'V_less'}, {name:'Voiced'},{name:'Bend'}],
        current: [
        ],
        //
        V_less: [
          [
            {name: 'a', left: '', right: '', raw: 'a.wav'} ,{name: 'i', left: '', right: '', raw: 'i.wav'},
            {name: 'u', left: '', right: '', raw: 'u.wav'},{name: 'e', left: '', right: '', raw: 'e.wav'},
            {name: 'o', left: '', right: '', raw: 'o.wav'}
          ],

          [
            {name: 'ka', left: '', right: '', raw: 'ka.wav'}, {name: 'ki', left: '', right: '', raw: 'ki.wav'},
            {name: 'ku', left: '', right: '', raw: 'ku.wav'}, {name: 'ke', left: '', right: '', raw: 'ke.wav'},
            {name: 'ko', left: '', right: '', raw: 'ko.wav'}
          ],

          [
            {name: 'sa', left: '', right: '', raw: 'sa.wav'},{name: 'shi', left: '', right: '', raw: 'shi.wav'},
            {name: 'su', left: '', right: '', raw: 'su.wav'},{name: 'se', left: '', right: '', raw: 'se.wav'},
            {name: 'so', left: '', right: '', raw: 'so.wav'}
          ],
          [
            {name: 'ta', left: '', right: '', raw: 'ta.wav'}, {name: 'chi', left: '', right: '', raw: 'chi.wav'},
            {name: 'tsu', left: '', right: '', raw: 'tsu.wav'}, {name: 'te', left: '', right: '', raw: 'te.wav'},
            {name: 'to', left: '', right: '', raw: 'to.wav'}
          ],
          [
            {name: 'na', left: '', right: '', raw: 'na.wav'},{name: 'ni', left: '', right: '', raw: 'ni.wav'},
            {name: 'nu', left: '', right: '', raw: 'nu.wav'},{name: 'ne', left: '', right: '', raw: 'ne.wav'},
            {name: 'no', left: '', right: '', raw: 'no.wav'}
          ],
          [
            {name: 'ha', left: '', right: '', raw: 'ha.wav'}, {name: 'hi', left: '', right: '', raw: 'hi.wav'},
            {name: 'fu', left: '', right: '', raw: 'fu.wav'}, {name: 'he', left: '', right: '', raw: 'he.wav'},
            {name: 'ho', left: '', right: '', raw: 'ho.wav'}
          ],
          [
            {name: 'ma', left: '', right: '', raw: 'ma.wav'},{name: 'mi', left: '', right: '', raw: 'mi.wav'},
            {name: 'mu', left: '', right: '', raw: 'mu.wav'},{name: 'me', left: '', right: '', raw: 'me.wav'},
            {name: 'mo', left: '', right: '', raw: 'mo.wav'}
          ],
          [
            {name: 'ya', left: '', right: '', raw: 'ya.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'yu', left: '', right: '', raw: 'yu.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'yo', left: '', right: '', raw: 'yo.wav'}
          ],

          [
            {name: 'ra', left: '', right: '', raw: 'ra.wav'}, {name: 'ri', left: '', right: '', raw: 'ri.wav'},
            {name: 'ru', left: '', right: '', raw: 'ru.wav'}, {name: 're', left: '', right: '', raw: 're.wav'},
            {name: 'ro', left: '', right: '', raw: 'ro.wav'},
          ],
          [
            {name: 'wa', left: '', right: '', raw: 'wa.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'wo', left: '', right: '', raw: 'wo.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'n', left: '', right: '', raw: 'n.wav'},
          ]
        ],
        //
        Voiced: [
          [
            {name: 'ga', left: '', right: '', raw: 'ga.wav'},{name: 'gi', left: '', right: '', raw: 'gi.wav'},
            {name: 'gu', left: '', right: '', raw: 'gu.wav'},{name: 'ge', left: '', right: '', raw: 'ge.wav'},
            {name: 'go', left: '', right: '', raw: 'go.wav'}
          ],
          [
            {name: 'za', left: '', right: '', raw: 'za.wav'},{name: 'zi', left: '', right: '', raw: 'ji.wav'},
            {name: 'zu', left: '', right: '', raw: 'zu.wav'},{name: 'ze', left: '', right: '', raw: 'ze.wav'},
            {name: 'zo', left: '', right: '', raw: 'zo.wav'}
          ],
          [
            {name: 'da', left: '', right: '', raw: 'da.wav'},{name: 'di', left: '', right: '', raw: 'ji.wav'},
            {name: 'du', left: '', right: '', raw: 'zu.wav'},{name: 'de', left: '', right: '', raw: 'de.wav'},
            {name: 'do', left: '', right: '', raw: 'do1.wav'}
          ],
          [
            {name: 'ba', left: '', right: '', raw: 'ba.wav'},{name: 'bi', left: '', right: '', raw: 'bi.wav'},
            {name: 'bu', left: '', right: '', raw: 'bu.wav'},{name: 'be', left: '', right: '', raw: 'be.wav'},
            {name: 'bo', left: '', right: '', raw: 'bo.wav'}
          ],
          [
            {name: 'pa', left: '', right: '', raw: 'pa.wav'},{name: 'pi', left: '', right: '', raw: 'pi.wav'},
            {name: 'pu', left: '', right: '', raw: 'pu.wav'},{name: 'pe', left: '', right: '', raw: 'pe.wav'},
            {name: 'po', left: '', right: '', raw: 'po.wav'}
          ]

        ],
        //
        Bend: [
          [
            {name: 'kya', left: '', right: '', raw: 'kya.wav'}
            ,{name: 'kyu', left: '', right: '', raw: 'kyu.wav'}
            ,{name: 'kyo', left: '', right: '', raw: 'kyo.wav'}
          ],
          [
            {name: 'gya', left: '', right: '', raw: 'gya.wav'}
            ,{name: 'gyu', left: '', right: '', raw: 'gyu.wav'},
            {name: 'gyo', left: '', right: '', raw: 'gyo.wav'}
          ],
          [
            {name: 'sha', left: '', right: '', raw: 'sha.wav'}
            ,{name: 'shu', left: '', right: '', raw: 'shu.wav'},
            {name: 'sho', left: '', right: '', raw: 'sho.wav'}
          ],
          [
            {name: 'jya', left: '', right: '', raw: 'jya.wav'}
            ,{name: 'jyu', left: '', right: '', raw: 'jyu.wav'},
            {name: 'jyo', left: '', right: '', raw: 'jyo.wav'}
          ],

          [
            {name: 'cya', left: '', right: '', raw: 'cha.wav'}
            ,{name: 'cyu', left: '', right: '', raw: 'chu.wav'},
            {name: 'cyo', left: '', right: '', raw: 'cho.wav'}
          ],

          [
            {name: 'hya', left: '', right: '', raw: 'hya.wav'}
            ,{name: 'hyu', left: '', right: '', raw: 'hyu.wav'},
            {name: 'hyo', left: '', right: '', raw: 'hyo.wav'}
          ],

          [
            {name: 'nya', left: '', right: '', raw: 'nya.wav'}
            ,{name: 'nyu', left: '', right: '', raw: 'nyu.wav'},
            {name: 'nyo', left: '', right: '', raw: 'nyo.wav'}
          ],

          //
//          [
//            {name: 'hya', left: '', right: '', raw: 'hya.wav'}
//            ,{name: 'hyu', left: '', right: '', raw: 'hyu.wav'},
//            {name: 'hyo', left: '', right: '', raw: 'hyo.wav'}
//          ],

          [
            {name: 'bya', left: '', right: '', raw: 'bya.wav'}
            ,{name: 'byu', left: '', right: '', raw: 'byu.wav'},
            {name: 'byo', left: '', right: '', raw: 'byo.wav'}
          ],
          [
            {name: 'pya', left: '', right: '', raw: 'pya.wav'}
            ,{name: 'pyu', left: '', right: '', raw: 'pyu.wav'},
            {name: 'pyo', left: '', right: '', raw: 'pyo.wav'}
          ],
          [
            {name: 'mya', left: '', right: '', raw: 'mya.wav'}
            ,{name: 'myu', left: '', right: '', raw: 'myu.wav'},
            {name: 'myo', left: '', right: '', raw: 'myo.wav'}
          ],
          [
            {name: 'rya', left: '', right: '', raw: 'rya.wav'}
            ,{name: 'ryu', left: '', right: '', raw: 'ryu.wav'},
            {name: 'ryo', left: '', right: '', raw: 'ryo.wav'}
          ],


        ]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

  .nav_bg {
    width:96%;
    text-align: center;
    padding-left: 3%;
    padding-right: 1%;
    /*border-radius: 0em 0em 1em 1em;*/
    box-shadow: 0.1em 0.3em 0.3em #072927;
    margin-bottom: 0.3em;
    background: #f0ba02;
    /*background: linear-gradient(#f0ba02, #cd7015, #f0ba02);*/
  }

  .nav{
    width: 4em;
  }

  .nav_btn{
    float: right;
    margin-right: 8%;
    margin-top: 0.5em;
    font-size: 1.2em;
    color: white;
    font-weight: bold;
    background: transparent;
  }
  .nav_item{
    text-align: center;
    margin-top: 0.5em;
    padding: 5px;
    font-size: 1em;
    background: #cd7015;
    border-color: brown;
  }
  .left_radius{
    border-radius: 1em 0em 0em 1em;
  }

  .right_radius{
    border-radius: 0em 1em 1em 0em;
  }

  .nav_active{
    background: white;
    border-color: #cd7015;
    color: coral;
    font-weight:bold;
  }

  .Japanese{
    padding-bottom: 1%;
  }
  .container{
    width:100%;
    text-align: center;
    margin-bottom: 5%;
    /*border-spacing: inherit;*/
    background: gray;
    empty-cells: show;
  }
  .container tr {
    text-align: center;
    /*background-color: #21ff13;*/
    display: flex;
  }

  .normal_row{
    text-align: center;
    width:20%;
  }

  .less_row{
    width:33%;
    text-align: center;
  }


  .letter{
    margin: 0%;
    font-size: 1.4em;
    line-height: 1.5em;
    color: #787250;
    background: #F8F6EA;
    padding-top: 0.4em;
    padding-bottom: 0.2em;
  }

  .active{
    font-size: 2.0em;
    line-height: 0.87em;
    color: coral;
    font-weight:bold;
  }

  #name{
    margin: 0%;
    font-size: 1.4em;
    line-height: 1.5em;
    color: #928B64;
    background: #EAE7D1;
    padding-bottom: 1%;
    font-size: 1em;
  }

  .box {
    position: absolute;
    width: 80px;
    height: 50px;
    background-color: #09f;
    /*display: none;*/
  }

</style>



